.form {
   & > *:not(:last-child) {
      margin: 0px 0px 30px 0px;
   }
   .input {
      position: relative;
      input {
         width: 100%;
         padding: 10px;
         background: transparent;
         border-bottom: 1px solid $grey;
         color: $white;
      }
      .error {
         position: absolute;
         left: 0;
         bottom: -25px;
         font-size: 14px;
         color: $grey;
      }
   }
   button {
      margin: 55px 0px 0px 0px;
   }
}
