.quotes {
   padding: 0px 0px 78px 0px;
   min-height: 30px;
   max-height: 400px;
   overflow-y: auto;
   &__row {
      display: flex;
      flex-direction: column;
      & > *:not(:last-child) {
         margin: 0px 0px 26px 0px;
      }
   }
   &__item {
      display: flex;
      align-items: center;
      padding: 22px 30px;
      border-radius: 20px;
      color: $black;
      div {
         max-width: 180px;
      }
      h1 {
         font-size: 25px;
         color: $primary;
      }
      p {
         margin: 0px 0px 16px 0px;
         font-size: 15px;
      }
   }
}
