body {
   font-family: "Alegreya Sans";
   font-weight: 500;
   font-size: 20px;
   @include lh(24, 20);
   scroll-behavior: smooth;
   background: $primary;
   color: $white;
}

h1 {
   font-family: "Alegreya";
   font-weight: 700;
   font-size: 30px;
   @include lh(41, 30);
}

.error {
   color: $error;
}

.logo svg {
   fill: $white;
}

/* ----------------CONTAINER------------------------------------------------- */

._container {
   max-width: 1230px;
   padding: 0 15px;
   margin: 0 auto;
}

