.onboarding-page {
   width: 100%;
   height: 100vh;
   text-align: center;
   overflow-y: auto;
   &__body {
      padding: 20px 0;
   }
   &__logo {
      margin: 0px 0px 32px 0px;
      svg {
         height: 199px;
         width: 191px;
      }
   }
   h1 {
      font-size: 34px;
      text-transform: uppercase;
   }
   &__phrases {
      margin: 0px 0px 95px 0px;
   }
   &__auth {
      button {
         margin: 0px 0px 18px 0px;
      }
      .no-account {
         font-weight: 400;
         a {
            font-weight: 500;
            color: inherit;
         }
      }
   }
}
