.image-gallery {
   padding: 17px 0px 78px 0px;
   min-height: 30px;
   max-height: 400px;
   overflow-y: auto;
   &__list {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 20px;
   }
   &__item {
      position: relative;
      border-radius: 20px;
      overflow: hidden;
      img {
         object-fit: cover;
         width: 153px;
         height: 115px;
      }
      span {
         position: absolute;
         left: 17px;
         bottom: 23px;
         font-size: 18px;
      }
   }
   & .add {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 153px;
      height: 115px;
      background: $lime;
      font-size: 40px;
      cursor: pointer;
      & input[type="file"] {
         position: absolute;
         z-index: -1;
         opacity: 0;
         display: block;
         width: 0;
         height: 0;
      }
   }
}
