.navbar-menu-bottom {
   padding: 27px 0;
   position: fixed;
   left: 0;
   bottom: 0;
   width: 100%;
   background: $primary;
   &__list {
      display: flex;
      justify-content: space-around;
      & > *:not(:last-child) svg {
         fill: $white;
      }
      & > *:not(:last-child) a.active svg {
         fill: $grey;
      }
      & > *:last-child svg {
         fill: transparent;
         stroke: $white;
      }
      & > *:last-child a.active svg {
         stroke: $grey;
      }
   }
   &__item {
      margin: -10px;
      padding: 10px;
      a {
         display: flex;
         align-items: center;
         justify-content: center;
      }
      svg {
         width: 24px;
         height: 24px;
      }
   }
}
