.btn {
   display: flex;
   justify-content: center;
   align-items: center;
   cursor: pointer;
   width: 100%;
   font-weight: 500;
   color: $white;
   border-radius: 10px;
}

.btn-primary {
   padding: 16px 0px;
   background: $green;
   font-size: 25px;
   @include lh(30, 25);
}

.btn-about {
   padding: 10px 0px;
   background: $primary;
   font-size: 15px;
   @include lh(18, 15);
}
