.feelings {
   padding: 0px 0px 22px 0px;
   h1 {
      margin: 30px 0px 2px 0px;
   }
   &__subtitle {
      margin: 0px 0px 27px 0px;
      font-size: 22px;
      color: $grey;
   }
   &__row {
      display: flex;
      align-items: center;
      overflow-x: auto;
      margin: 0px -8.5px;
   }
   &__column {
      padding: 0px 8.5px;
   }
   &__item {
      display: flex;
      align-items: center;
      flex-direction: column;
      div {
         display: flex;
         align-items: center;
         justify-content: center;
         width: 65px;
         height: 65px;
         background: $white;
         border-radius: 20px;
      }
      span {
         font-size: 12px;
      }
   }
}
