.full-image-page {
   width: 100%;
   height: 100vh;
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   img {
      width: 100%;
      height: 100%;
      object-fit: contain;
   }
}

.image-navbar-control-menu-bottom {
   position: fixed;
   left: 0;
   bottom: 0;
   width: 100%;
   height: 60px;
   display: flex;
   align-items: center;
   justify-content: space-evenly;
   background: $primary;
   button {
      padding: 5px 20px;
      width: auto;
   }
}
