.request-access {
   width: 100%;
   height: 100vh;
   overflow-y: auto;
   &__container {
      height: inherit;
   }
   &__body {
      padding: 20px 0;
      height: inherit;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
   }
   .iframe {
      position: relative;
      &::after {
         content: "Сначала нажмите на кнопку получения доступа. После 'Далее'";
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 130px;
         background: $primary;
         z-index: 1000;
         text-align: center;
      }
      &::before {
         content: "";
         position: absolute;
         bottom: 0;
         left: 0;
         width: 100%;
         height: 90px;
         background: $primary;
         z-index: 1000;
      }
   }
}
