.header {
   padding: 60px 0px 0px 0px;
   &__body {
      display: flex;
      align-items: center;
      justify-content: space-between;
   }
   a {
      padding: 0px 10px;
   }
   &__burger {
      svg {
         width: 24px;
         height: 24px;
      }
   }
   &__logo {
      svg {
         width: 43px;
         height: 43px;
      }
   }
   img {
      object-fit: cover;
      width: 35px;
      height: 35px;
      border-radius: 50%;
      overflow: hidden;
   }
   button {
      padding: 10px 0px;
      font-size: 14px;
      width: auto;
   }
}
